.cart-dropdown{
    position: absolute;
    width: 240px;
    height: 340px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid white;
    background-color: #2B2B2B;
    top: 80px;
    right: 0;
    z-index: 5;
    
    .empty-message{
        font-size: 18px;
        margin: 50px auto;
    }

    .cart-items{
        height: 240px;
        display: flex;
        flex-direction: column;
        overflow:scroll;
    }

    button{
        margin-top: auto;
    }
}