.menu-item{
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid white;
    margin: 0 7.5px 15px;
    overflow: hidden;

    &:hover{
        cursor: pointer;

        & .background-image {
            transform: scale(1.1);
            transition: transform 6s cubic-bezier(.17,.67,.65,1.72);
        }

        &.content {
            opacity: 0.9;
        }
    }

    &.large{
        height: 380px;
    }

    &:first-child{
        margin-right: 7.5px;
    }
    
    .background-image{
        width: 100%;
        height: 100%;
        background-position: center;
        background-size: cover;     
    }

    &:last-child {
        margin-left: 7.5px;
    }

    .content{
        height: 90px;
        padding: 0 25px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border: 1px solid white;
        position: absolute;
        background-color: #2B2B2B;
        opacity: 0.6;
    }

    .hats{
        font-weight: bold;
        margin-bottom: 6px;
        font-size: 22px;
        color: #ededed;
        z-index: 1;
        font-weight: 900;
    }

    .subtitle{
        font-weight: lighter;
        font-size: 16px;
        z-index:2;
        font-weight: 900;
    }

}
