.header {
    height: 70px;
    width:100%;
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
   
    .logo-container{
        height:20%;
        width: 100%;
        padding: 10px;

        .logo{
            width: 100px;
            height:100px;
        }
    }

    .options{
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        .option{
            padding: 10px 15px;
            cursor: pointer;
        }
    }
}
